.blog-button {
    margin: 16px;
    cursor: pointer;

    -webkit-box-shadow: -3px 1px 18px 0 #adadad;

            box-shadow: -3px 1px 18px 0 #adadad;
    text-decoration: none;
}

.blog-button:hover {
    -webkit-box-shadow: -3px 1px 9px 5px #adadad;
            box-shadow: -3px 1px 9px 5px #adadad;
}

.blog-button:active {
    color: black;
}


